<template>
  <div id="app"
    class="is-flex"
    style="padding: 1rem; margin-top: 0.5rem; height: 100vh; width: 100vw"
  >
    <div>
      <div class="control" style="width: 240px; max-width: 240px; overflow: hidden">
        <label class="label">区间长度参数</label>
        <input type="text" class="input" v-model="params.sectionLength">
      </div>
      <div style="height: 0.75rem"></div>
      <div class="file is-boxed">
        <label class="file-label">
          <input class="file-input" type="file" @input="handleFilesInput" multiple>
          <span class="file-cta">
            <span class="file-icon">
              <i class="iconfont icon-upload-page"></i>
            </span>
            <span class="file-label">上传文件</span>
          </span>
        </label>
      </div>

      <div style="height: 0.75rem"></div>

      <FileList
        :files="files"
        :currentFile="currentFile"
        @click-file="currentFile = $event"
      />
    </div>

    <div style="min-width: 3rem"></div>
    
    <FileChart
      :params="params"
      :file="currentFile"
    />
  </div>
</template>

<script>
import FileList from './FileList'
import FileChart from './FileChart'

export default {
  name: 'app',
  components: {
    FileList,
    FileChart,
  },
  data: () => ({
    files: [],
    currentFile: null,
    params: {
      sectionLength: 100
    },
  }),
  methods: {
    handleFilesInput (e) {
      this.files = e.target.files
      if (this.files.length) {
        this.currentFile = this.files[0]
      }
    }
  }
}
</script>

