<template>
  <nav v-if="files.length" class="panel" style="max-width: 240px">
    <p class="panel-heading is-size-6">
      文件列表
    </p>
    <a
      class="panel-block is-active"
      :class="file === currentFile && 'has-border-right-info'"
      v-for="(file, key) in files" :key="key"
      @click="handleClick(file)"
    >
      <span style="margin-right: 0.25rem">{{file.name}}</span>
    </a>
  </nav>
</template>

<script>
export default {
  name: 'FileList',
  props: {
    files: {

    },
    currentFile: {

    }
  },
  methods: {
    handleClick (file) {
      this.$emit("click-file", file)
    }
  }
}
</script>
