<template>
  <div v-if="file" class="is-flex-auto">
    <div id="chart" style="width: 100%; height: 512px"></div>

    <div style="height: 1rem"></div>

    <table class="table is-centered is-nowrap is-fullwidth">
      <thead>
        <th style="width: 1px">#</th>
        <th>最高峰</th>
        <th>最低谷</th>
        <th style="width: 1px">差值（最高峰 - 最低谷）</th>
        <th>起始日期</th>
        <th>结束日期</th>
        <th>持续月数</th>
      </thead>
      <tbody>
        <tr v-for="({times, months, years, monthLength}, key) in sections" :key="key">
          <td>{{ key + 1 }}</td>
          <td>{{ times[1] }}</td>
          <td>{{ times[0] }}</td>
          <td>{{ times[1] - times[0] }}</td>
          <td>{{ `${years[0]} 年 ${months[0]} 月` }}</td>
          <td>{{ `${years[1]} 年 ${months[1]} 月` }}</td>
          <td>{{ monthLength + ' 个月' }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import xlsx from 'xlsx'
import echarts from 'echarts'

export default {
  name: 'Chart',
  props: {
    file: {},
    params: {},
  },
  data: () => ({
    sections: []
  }),
  watch: {
    file: {
      immediate: true,
      handler (file) {
        if (!file) return

        const reader = new FileReader()

        reader.readAsBinaryString(file)
        reader.onloadend = () => {
          const result = reader.result
          const workbook = xlsx.read(result, { type: 'binary' })
          const sheet = workbook.Sheets[workbook.SheetNames[0]]

          if (sheet) {
            const name = file.name
            const parseOptions = { header: 1, raw: false }
            const rowObject = xlsx.utils.sheet_to_json(sheet, parseOptions);
            const dataSet = getMonthFrequent(rowObject)
            const chartOptions = getChartOptions(dataSet, name)
            const sections = getDataSections(dataSet)
              .filter(section => (section.times[1] - section.times[0]) > this.params.sectionLength)

            this.sections = sections
            initEcharts(chartOptions)
            this.isShowResult = true
          }
        }
      }
    }
  }
}

function getMonthFrequent (dataSet) {
  dataSet = dataSet.slice(3)
  dataSet = dataSet.map(mapDataRow)

  return dataSet.reduce(dataSetReduce, [])
}

function mapDataRow (dataRow) {
  var date = dataRow[0]
  var times = Number.parseInt(dataRow[1])

  date = date.split('/')

  const year = date[2]
  const month = date[0]

  return { year, month, times }
}

function dataSetReduce (dataSet, dataItem) {
  const { year, month, times } = dataItem
  const item = dataSet[dataSet.length - 1]

  if (!item || item.month !== month || item.year !== year) {
    dataSet.push({ year, month, times })
  } else {
    item.times += times
  }

  return dataSet
}

/**
 * { start: 100, end: 50, months: 5 }
 */
function getDataSections (data) {
  return data.reduce((sections, item) => {
    const { year, month, times } = item
    const section = sections[sections.length - 1]
    const trend = section ? section.times[1] - section.times[0] : null

    if (
      !section ||
      (trend < 0 && times > section.times[1]) ||
      (trend > 0 && times < section.times[1])
    ) {
      sections.push({
        years: [section ? section.years[1] : year, year],
        months: [section ? section.months[1] : month, month],
        times: [section ? section.times[1] : times, times],
        monthLength: section ? 1 : 0,
      })
    } else {
      section.years[1] = year
      section.months[1] = month
      section.times[1] = times
      section.monthLength++
    }

    return sections
  }, [])
}

function getChartOptions (dataSet, name) {
  return {
    title: {
      text: `商品名: ${name}`
    },
    tooltip: {
      trigger: 'axis'
    },
    xAxis: {
      data: dataSet.map(data => `${data.year}年${data.month}月`)
    },
    yAxis: {
      splitLine: {
        show: false
      }
    },
    toolbox: {
      left: 'center',
      feature: {
        // dataZoom: {
        //   yAxisIndex: 'none'
        // },
        // restore: {},
        // saveAsImage: {}
      }
    },
    dataZoom: [{
      // startValue: '2014-06-01'
    }, {
      type: 'inside'
    }],
    visualMap: {
      top: 10,
      right: 10,
      outOfRange: {
        color: '#999'
      }
    },
    series: {
      name: 'times',
      type: 'line',
      data: dataSet.map(data =>  data.times)
    }
  }
}

function initEcharts(options) {
  const chart = echarts.init(document.getElementById('chart'))

  chart.setOption(options)
}
</script>
